import React, { useEffect, useRef } from "react";
import flatpickr from "flatpickr";
import cx from "classnames";
import "flatpickr/dist/flatpickr.min.css";
import classes from "./index.module.scss";

export type DateRange = {
  from: Date;
  to: Date;
};

type Props = {
  value: DateRange;
  onChange: (value: DateRange) => void;
};

function DateRangePicker({ value, onChange }: Props): JSX.Element {
  const containerEl = useRef<HTMLInputElement>(null);
  const flatPickrInstance = useRef<flatpickr.Instance | null>(null);
  useEffect(() => {
    flatPickrInstance.current = flatpickr(containerEl.current as HTMLInputElement, {
      mode: "range",
      maxDate: new Date(),
      dateFormat: "Y-m-d",
      allowInput: true,
      onChange: ([newFrom, newTo]: Date[]) => {
        if (newFrom && newTo) {
          onChange({ from: newFrom, to: newTo });
        }
      },
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (flatPickrInstance.current) {
      flatPickrInstance.current.setDate([value.from, value.to], false);
    }
  }, [value.from, value.to]);

  return (
    <input className={cx(classes.container, "input")} ref={containerEl} type="text" placeholder="Select a range" />
  );
}

export default DateRangePicker;
