import React from "react";
import cx from "classnames";
import classes from "./index.module.scss";

type Props = {
  className?: string;
  pages: number;
  current: number;
  // thumbnails?: number;
  onChange: (page: number) => void;
};
function PageNav(props: Props): JSX.Element | null {
  const { className, pages, current /* , thumbnails */, onChange } = props;

  if (pages <= 0) {
    return null;
  }

  return (
    <nav className={cx(className, classes.container, "pagination")}>
      <ul className="pagination-list">
        {Array.from({ length: pages }, (v, i) => i + 1).map((page) => {
          return (
            <li key={page.toString()}>
              <button
                type="button"
                aria-label={`Goto page ${page}`}
                className={cx("pagination-link", { "is-current": current === page })}
                onClick={onChange.bind(null, page)}
              >
                {page}
              </button>
            </li>
          );
        })}
        {/* <li>
            <a class="pagination-link" aria-label="Goto page 1">1</a>
            </li>
            <li>
            <span class="pagination-ellipsis">&hellip;</span>
            </li>
            <li>
            <a class="pagination-link" aria-label="Goto page 45">45</a>
            </li> */}
      </ul>
    </nav>
  );
}

PageNav.defaultProps = {
  className: "",
  // thumbnails: 9,
};

export default PageNav;
