/* eslint-disable no-nested-ternary */
import React, { useCallback, useState, useMemo } from "react";
import ReactDOM from "react-dom";
// import cx from "classnames";
import * as am4core from "@amcharts/amcharts4/core";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import dayjs from "dayjs";
import {
  getDashboardPeriodData,
  getDashboardVersionData,
  getDashboardRegionData,
  getCommentCountByWinkScore,
  getCommentCountByRegionWinkScore,
} from "../../../api/commentapi";
import LeftBar from "./leftbar/LeftBar";
import ChartView from "../../../components/charts/ChartView";
import TableView from "../../../components/tables/TableView";
import CommentInfoCard from "../../../components/cards/CommentInfoCard";
import regions from "../../../common/regions/regions";
import emojimaps from "../../../common/emotions/emotions";
import classes from "./dashboard.module.scss";
import { ReactComponent as MenuDashboardIcon } from "../../../assets/common/icons/dashboard.svg";

const colormaps = [
  "#487ecf",
  "#fe57ca",
  "#c5e17b",
  "#e69641",
  "#6eb8b3",
  "#aaaaaa",
  "#eebe32",
  "#de5e5c",
  "#883cd4",
  "#f2e406",
  "#439070",
];

const regioncolormaps: any = {
  APAC: "#487ecf",
  NAFTA: "#fe57ca",
  EMEA: "#c5e17b",
  GC: "#e69641",
};

type DateRange = {
  from: Date;
  to: Date;
};

function CommentCardList(props: any) {
  const { config } = props;
  const listItems = config.map((itemconfig: any) => <CommentInfoCard config={itemconfig} />);
  return <div className={classes.listarea}>{listItems}</div>;
}

function PageItem(): JSX.Element {
  const enddate = new Date();
  const startdate = new Date();
  startdate.setDate(startdate.getDate() - 30);
  const [dateRange, setDateRange] = useState<DateRange>({
    from: startdate,
    to: enddate,
  });

  // const [exportDateRange, setExportDateRange] = useState<DateRange>({ from: new Date(), to: new Date() });

  const getCommentRegionWinksocreData = useCallback(() => {
    return getCommentCountByRegionWinkScore()
      .then((value) => {
        for (let i = 0; i < value.list.length; i += 1) {
          if (value.list[i].winkscore > 5) value.list[i].winkscore = 5; // eslint-disable-line no-param-reassign
          if (value.list[i].winkscore < -5) value.list[i].winkscore = -5; // eslint-disable-line no-param-reassign
          if (!value.list[i].winkscore) value.list[i].winkscore = 0; // eslint-disable-line no-param-reassign
        }
        for (let i = value.list.length - 1; i >= 0; i -= 1) {
          for (let j = 0; j < i; j += 1) {
            if (value.list[i].winkscore === value.list[j].winkscore && value.list[i].region === value.list[j].region) {
              value.list[j].count += value.list[i].count; // eslint-disable-line no-param-reassign
              value.list.splice(i, 1); // eslint-disable-line no-param-reassign
              break;
            }
          }
        }
        for (let i = 0; i < value.list.length; i += 1) {
          value.list[i].name = emojimaps[value.list[i].winkscore + 5]; // eslint-disable-line no-param-reassign
        }

        const columns = [
          {
            Header: "Region",
            accessor: "region",
          },
          {
            Header: "Sentimental Degree",
            accessor: "winkscore",
          },
          {
            Header: "Sentimental Degree Name",
            accessor: "name",
          },
          {
            Header: "Comment count",
            accessor: "count",
          },
        ];

        ReactDOM.render(
          <TableView columndata={columns} tabledata={value.list} />,
          document.getElementById("domregiontable")
        );
      })
      .catch(function (err) {
        // setPeriodDataSource({});
      });
  }, []);

  const getCommentCategoryCard = useCallback(() => {
    return getCommentCountByWinkScore()
      .then((value) => {
        for (let i = 0; i < value.list.length; i += 1) {
          if (value.list[i].winkscore > 5) value.list[i].winkscore = 5; // eslint-disable-line no-param-reassign
          if (value.list[i].winkscore < -5) value.list[i].winkscore = -5; // eslint-disable-line no-param-reassign
          if (!value.list[i].winkscore) value.list[i].winkscore = 0; // eslint-disable-line no-param-reassign
        }

        for (let i = value.list.length - 1; i >= 0; i -= 1) {
          for (let j = 0; j < i; j += 1) {
            if (value.list[i].winkscore === value.list[j].winkscore) {
              value.list[j].count += value.list[i].count; // eslint-disable-line no-param-reassign
              value.list.splice(i, 1); // eslint-disable-line no-param-reassign
              break;
            }
          }
        }
        for (let i = 0; i < value.list.length; i += 1) {
          value.list[i].name = emojimaps[value.list[i].winkscore + 5]; // eslint-disable-line no-param-reassign
        }

        for (let i = 0; i < 11; i += 1) {
          let findit = false;
          for (let j = 0; j < value.list.length; j += 1) {
            if (value.list[j].winkscore === i - 5) {
              findit = true;
              break;
            }
          }

          if (!findit) {
            value.list.push({
              count: 0,
              winkscore: i - 5,
              name: emojimaps[i],
            });
          }
        }

        const sortedlist = [];
        for (let i = 5; i >= -5; i -= 1) {
          for (let j = 0; j < value.list.length; j += 1) {
            if (value.list[j].winkscore === i) {
              sortedlist.push(value.list[j]);
            }
          }
        }
        ReactDOM.render(<CommentCardList config={sortedlist} />, document.getElementById("domemojiarea"));
      })
      .catch(function (err) {
        // setPeriodDataSource({});
      });
  }, []);

  const getCommentRegionData = useCallback(() => {
    return getDashboardRegionData()
      .then((value) => {
        const config: any = {
          type: "MapChart",
          geodata: am4geodata_worldLow,
          geodataSource: {
            url: "/path/to/myCustomMap.json",
          },
          projection: "Miller",
          series: [],
        };

        const tempitem: any = {
          type: "MapPolygonSeries",
          useGeodata: true,
          data: [],
          mapPolygons: {
            propertyFields: {
              fill: "fill",
            },
            tooltipText: "{name}: {value}",
          },
        };

        for (let i = 0; i < value.list.length; i += 1) {
          for (let j = 0; j < regions.length; j += 1) {
            if (regions[j].Grouping === value.list[i].region) {
              tempitem.data.push({
                id: regions[j].Code,
                name: regions[j].Name,
                value: value.list[i].count,
                fill: am4core.color(regioncolormaps[value.list[i].region]),
              });
            }
          }
          config.series.push(tempitem);
        }
        const element = <ChartView config={config} />;
        ReactDOM.render(element, document.getElementById("domregionchart"));
      })
      .catch(function (err) {
        // setPeriodDataSource({});
      });
  }, []);

  const getCommentVersionData = useCallback(() => {
    return getDashboardVersionData()
      .then((value) => {
        const config: any = {
          type: "XYChart",
          data: [],
          yAxes: [
            {
              type: "ValueAxis",
              title: {
                text: "Comments",
              },
            },
          ],
          xAxes: [
            {
              type: "CategoryAxis",
              title: {
                text: "App Versions",
              },
              dataFields: {
                category: "appversion",
              },
              renderer: {
                mlocation: 0,
                minGridDistance: 20,
              },
            },
          ],
          series: [],
          legend: {
            position: "top",
            useDefaultMarker: false,
          },
        };

        const PATTERN = "value";
        for (let i = 0; i < 11; i += 1) {
          config.series.push({
            type: "ColumnSeries",
            name: emojimaps[i],
            tooltipText: "{name}: [bold]{valueY}[/]",
            fill: colormaps[i],
            strokeWidth: 0,
            strokeOpacity: 1,
            stacked: true,
            dataFields: {
              valueY: PATTERN.concat(i.toString()),
              categoryX: "appversion",
            },
          });
        }

        for (let i = 0; i < value.list.length; i += 1) {
          if (value.list[i].winkscore > 5) value.list[i].winkscore = 5; // eslint-disable-line no-param-reassign
          if (value.list[i].winkscore < -5) value.list[i].winkscore = -5; // eslint-disable-line no-param-reassign
          if (!value.list[i].winkscore) value.list[i].winkscore = 0; // eslint-disable-line no-param-reassign
        }

        for (let i = value.list.length - 1; i >= 0; i -= 1) {
          for (let j = 0; j < i; j += 1) {
            if (
              value.list[i].appversion === value.list[j].appversion &&
              value.list[i].winkscore === value.list[j].winkscore
            ) {
              value.list[j].count += value.list[i].count; // eslint-disable-line no-param-reassign
              value.list.splice(i, 1); // eslint-disable-line no-param-reassign
              break;
            }
          }
        }

        for (let i = 0; i < value.list.length; i += 1) {
          let findit = false;
          for (let j = 0; j < config.data.length; j += 1) {
            if (value.list[i].appversion === config.data[j].appversion) {
              config.data[j][PATTERN.concat((value.list[i].winkscore + 5).toString())] = value.list[i].count;
              findit = true;
              break;
            }
          }

          if (!findit) {
            const tempitem: any = {
              appversion: value.list[i].appversion,
              value0: 0,
              value1: 0,
              value2: 0,
              value3: 0,
              value4: 0,
              value5: 0,
              value6: 0,
              value7: 0,
              value8: 0,
              value9: 0,
              value10: 0,
            };
            tempitem[PATTERN.concat((value.list[i].winkscore + 5).toString())] = value.list[i].count;
            config.data.push(tempitem);
          }
        }
        const element = <ChartView config={config} />;
        ReactDOM.render(element, document.getElementById("domversionchart"));
      })
      .catch(function (err) {
        // setPeriodDataSource({});
      });
  }, []);

  const getCommentPeriodData = useCallback(() => {
    return getDashboardPeriodData(dateRange)
      .then((value) => {
        const config: any = {
          type: "XYChart",
          data: [],
          yAxes: [
            {
              type: "ValueAxis",
              renderer: {
                grid: {
                  template: {
                    type: "Grid",
                    location: 0,
                  },
                },
              },
            },
          ],
          xAxes: [
            {
              type: "CategoryAxis",
              dataFields: {
                category: "category",
              },
              renderer: {
                maxLabelPosition: 0.98,
              },
            },
          ],
          series: [],
          legend: {
            position: "top",
          },
        };

        const PATTERN = "value";
        for (let i = 0; i < 11; i += 1) {
          config.series.push({
            type: "LineSeries",
            stroke: colormaps[i],
            strokeWidth: 3,
            name: emojimaps[i],
            columns: {
              template: {
                type: "Column",
                strokeOpacity: 0,
                tooltipText: "{categoryY}\n{valueX}",
                tooltipPosition: "pointer",
              },
            },
            dataFields: {
              valueY: PATTERN.concat(i.toString()),
              categoryX: "category",
            },
          });
        }
        for (let i = 0; i < value.list.length; i += 1) {
          if (value.list[i].winkscore > 5) value.list[i].winkscore = 5; // eslint-disable-line no-param-reassign
          if (value.list[i].winkscore < -5) value.list[i].winkscore = -5; // eslint-disable-line no-param-reassign
          if (!value.list[i].winkscore) value.list[i].winkscore = 0; // eslint-disable-line no-param-reassign
        }
        for (let i = value.list.length - 1; i >= 0; i -= 1) {
          for (let j = 0; j < i; j += 1) {
            if (
              value.list[i].eventdate === value.list[j].eventdate &&
              value.list[i].winkscore === value.list[j].winkscore
            ) {
              value.list[j].count += value.list[i].count; // eslint-disable-line no-param-reassign
              value.list.splice(i, 1); // eslint-disable-line no-param-reassign
              break;
            }
          }
        }
        for (let i = 0; i < value.list.length; i += 1) {
          const item = value.list[i];
          const tempitem: any = {
            category: item.eventdate,
            value10: 0,
            value9: 0,
            value8: 0,
            value7: 0,
            value6: 0,
            value5: 0,
            value4: 0,
            value3: 0,
            value2: 0,
            value1: 0,
          };
          if (item.winkscore !== 0) tempitem[PATTERN.concat((5 + item.winkscore).toString())] = item.count;
          config.data.push(tempitem);
        }
        const newdatas = [];
        const tmpstartDate = new Date(startdate.getTime());
        while (tmpstartDate.getTime() < enddate.getTime()) {
          let findit = false;
          for (let i = 0; i < config.data.length; i += 1) {
            if (config.data[i].category === dayjs(tmpstartDate).startOf("day").format("YYYY-MM-DD")) {
              newdatas.push(config.data[i]);
              findit = true;
            }
          }

          if (!findit) {
            newdatas.push({
              category: dayjs(tmpstartDate).startOf("day").format("YYYY-MM-DD"),
              value10: 0,
              value9: 0,
              value8: 0,
              value7: 0,
              value6: 0,
              value5: 0,
              value4: 0,
              value3: 0,
              value2: 0,
              value1: 0,
            });
          }
          tmpstartDate.setDate(tmpstartDate.getDate() + 1);
        }
        config.data = newdatas;
        const element = <ChartView config={config} />;
        ReactDOM.render(element, document.getElementById("domperiodchart"));
      })
      .catch(function (err) {
        // setPeriodDataSource({});
      });
  }, [dateRange]);

  getCommentPeriodData();
  getCommentVersionData();
  getCommentRegionData();
  getCommentCategoryCard();
  getCommentRegionWinksocreData();

  return (
    <div className={classes.container}>
      <div className={classes.leftpanel}>
        <LeftBar />
      </div>
      <div className={classes.chartpanel}>
        <div className={classes.rowarea_title}>
          <div className={classes.prodtitle}>
            <MenuDashboardIcon />
            <div className={classes.title}>ClickShare Desktop App</div>
          </div>
          <div className={classes.charttitle}>User Rating and Comments review</div>
        </div>
        <div className={classes.rowarea_content}>
          <div className={classes.chartarea}>
            <div className={classes.titlebar}>
              <div className={classes.title}>Trend</div>
              <div className={classes.rightoptions}>Monthly</div>
            </div>
            <div className={classes.content} id="domperiodchart" />
          </div>
        </div>
        <div className={classes.rowarea_content}>
          <div className={classes.emojiarea} id="domemojiarea" />
        </div>
        <div className={classes.rowarea_content}>
          <div className={classes.chartarea}>
            <div className={classes.titlebar}>
              <div className={classes.title}>By App Version</div>
            </div>
            <div className={classes.content} id="domversionchart" />
          </div>
        </div>
        <div className={classes.rowarea_content}>
          <div className={classes.chartarea}>
            <div className={classes.titlebar}>
              <div className={classes.title}>By Region</div>
            </div>
            <div className={classes.content} id="domregionchart" />
          </div>
        </div>
        <div className={classes.rowarea_content}>
          <div className={classes.chartarea}>
            <div className={classes.titlebar}>
              <div className={classes.title}>Region Detail</div>
            </div>
            <div className={classes.content} style={{ height: "auto" }} id="domregiontable" />
          </div>
        </div>
      </div>
    </div>
  );
}

export default PageItem;
