import axios, { AxiosResponse } from "axios";
import dayjs from "dayjs";
import * as apiurl from "./api-url-def";

export interface PeroidDataItem {
  count: number;
  winkscore: number;
  eventdate: string;
}

export interface VersionDataItem {
  count: number;
  winkscore: number;
  appversion: string;
}

export interface RegionDataItem {
  count: number;
  region: string;
}

export interface WinkScoreDataItem {
  count: number;
  winkscore: number;
  name: string;
}

export interface RegionWinkScoreDataItem {
  count: number;
  winkscore: number;
  region: string;
  name: string;
}

type DataResponse<T> = {
  result: {
    code: number;
    msg: string;
    datas?: T;
    tcount?: number;
  };
};

const axiosInstance = axios.create({
  baseURL: apiurl.getApiRootPath(),
  responseType: "json",
  headers: {
    "Content-Type": "application/json; charset=UTF-8",
  },
});

const getDashboardPeriodData = async (dateRange: {
  from: Date;
  to: Date;
}): Promise<{ list: PeroidDataItem[]; count: number }> => {
  const params = new URLSearchParams();
  params.append(
    "filters",
    JSON.stringify({
      daterange: {
        start: dayjs(dateRange.from).startOf("day").format("YYYY-MM-DD"),
        end: dayjs(dateRange.to).endOf("day").format("YYYY-MM-DD"),
      },
    })
  );

  const result: AxiosResponse<DataResponse<PeroidDataItem[]>> = await axiosInstance.post(
    apiurl.getAPIurl("cxdesktopapp_comment_period"),
    params,
    {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
    }
  );
  const { code, msg, datas = [], tcount = 0 } = result.data.result;
  if (code < 0) {
    throw new Error(msg);
  }
  return { list: datas, count: tcount };
};

