import qs from "qs";

export function downloadLink(url: string, params?: Record<string, unknown>): void {
  let paramsStr = "";
  if (params) {
    paramsStr = qs.stringify(params);
  }

  const anchor = document.createElement("a");
  anchor.setAttribute("href", `${url}${paramsStr ? `?${paramsStr}` : ""}`);
  anchor.setAttribute("target", "_blank");

  // Click event
  // let event = document.createEvent('MouseEvent')
  // event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0,
  //     false, false, false, false, 0, null)
  // anchor.dispatchEvent(event)
  anchor.click();
}

export function downloadWithForm(url: string, data: Record<string, string | Record<string, unknown>>): void {
  // create form
  const form = document.createElement("form");
  form.action = url;
  form.method = "POST";
  // must set response to a different window (or iframe)
  // otherwise after download, in ie9 all future button clicks will trigger download again
  form.target = "_blank";

  // Add data to form
  Object.entries(data).forEach(([key, value]) => {
    const node = document.createElement("input");
    node.name = key;
    node.value = typeof value === "string" ? value : JSON.stringify(value);
    form.appendChild(node);
  });

  form.style.display = "none";
  document.body.appendChild(form);

  // submit form
  form.submit();

  // Remove form after sent
  document.body.removeChild(form);
}
