import React, { useEffect, useState } from "react";
import cx from "classnames";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import * as am4maps from "@amcharts/amcharts4/maps";
import "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import { getNextID } from "../../common/utils/id";
import classes from "./ChartView.module.scss";

am4core.useTheme(am4themes_animated);
// Chart.register(...registerables);

type Props = { config: any };

function ChartView(props: Props): JSX.Element {
  const { config } = props;
  const chartdomid = getNextID();

  useEffect(() => {
    if (config) {
      if (config.type === "MapChart") {
        am4core.createFromConfig(config, chartdomid, am4maps.MapChart);
      } else {
        am4core.createFromConfig(config, chartdomid);
      }
    }
  }, []);
  return <div id={chartdomid} className={cx(classes.container)} />;
}

export default ChartView;
