// eslint-disable-next-line @typescript-eslint/no-explicit-any
type AnyApiFunction = (...args: any[]) => Promise<any>;
type UnPromisify<T> = T extends Promise<infer U> ? U : never;
type ApiReturnType<T extends AnyApiFunction> = UnPromisify<ReturnType<T>> | null;

function useFetch<T extends AnyApiFunction>(
  api: T
): {
  data: ApiReturnType<T>;
  fetch: (...args: Parameters<T>) => void;
  error: string;
  isLoading: boolean;
} {
  const [data, setData] = useState<ApiReturnType<T>>(null);
  const [error, setError] = useState<string>("");
  const [isLoading, setLoading] = useState(false);

  const fetch = useCallback(
    (...params) => {
      setError("");
      setData(null);
      setLoading(true);
      api(...params)
        .then((result) => {
          setData(result);
        })
        .catch((err) => {
          setError(err.message);
        })
        .finally(() => {
          setLoading(false);
        });
    },
    [api]
  );

  return { data, fetch, error, isLoading };
}

export default useFetch;
