interface UrlMap {
  name: string;
  url: string;
}

// const rootpath = "http://localhost:3001/apis/";
const rootpath = "https://dlc3.barco.com:3001/apis/";
const apimaps: Array<UrlMap> = [
  {
    name: "cxdesktopapp_comment_period",
    url: "clickshare/desktopapp/comment/dashboard/period",
  },
  {
    name: "cxdesktopapp_comment_appversion",
    url: "clickshare/desktopapp/comment/dashboard/version",
  },
  {
    name: "cxdesktopapp_comment_region",
    url: "clickshare/desktopapp/comment/dashboard/region",
  },
  {
    name: "cxdesktopapp_comment_winkscore",
    url: "clickshare/desktopapp/comment/dashboard/winkscoregroup",
  },
  {
    name: "cxdesktopapp_comment_regionwinkscore",
    url: "clickshare/desktopapp/comment/dashboard/regionwinkscoregroup",
  },
  {
    name: "cxdesktopapp_comment_history",
    url: "clickshare/desktopapp/comment/history",
  },
  {
    name: "cxdesktopapp_comment_historydownload",
    url: "clickshare/desktopapp/comment/downloadfile",
  },
];

function getAPIurl(name: string): string {
  for (let i = 0; i < apimaps.length; i += 1) {
    if (apimaps[i].name === name) return apimaps[i].url;
  }
  return "";
}

function getApiRootPath() {
  return rootpath;
}

export { getApiRootPath, getAPIurl };
