/* eslint-disable react/jsx-props-no-spreading */
import React, { useState, useEffect } from "react";
import cx from "classnames";
import { useTable, Column } from "react-table";
import classes from "./TableView.module.scss";

type Props = { columndata: any; tabledata: any };
/*
interface IData {
  col1: string;
  col2: string;
  col3: string;
}
*/
function TableView(props: Props): JSX.Element {
  const { columndata, tabledata } = props;
  const data: any = React.useMemo(() => tabledata, []);
  const columns: any = React.useMemo(() => columndata, []);
  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable({ columns, data });

  return (
    <div>
      <table className={cx(classes.container)} {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>{column.render("Header")}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return <td {...cell.getCellProps()}>{cell.render("Cell")}</td>;
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

export default TableView;
