import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import classes from "./App.module.scss";
import pageClickShareCommentHistory from "./pages/clickshare/comment/history";
import pageClickShareCommentDashboard from "./pages/clickshare/comment/dashboard";

function App(): JSX.Element {
  return (
    <div className={classes.container}>
      <BrowserRouter>
        <Switch>
          <Route path="/clickshare/comment/dashboard" component={pageClickShareCommentDashboard} />
          <Route path="/clickshare/comment/history" component={pageClickShareCommentHistory} />
          <Route path="/" component={pageClickShareCommentHistory} />
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App;
