import axios, { AxiosResponse } from "axios";
import dayjs from "dayjs";
import https from "https";
import * as apiurl from "./api-url-def";
import { downloadWithForm } from "../common/utils/download";

export interface Comment {
  appversion: string;
  comment: string;
  commentid: string;
  eventtime: string;
  language: string;
  osinfo: string;
  emscore: number;
  rating: number;
}

type BiResponse<T> = {
  result: {
    code: number;
    msg: string;
    datas?: T;
    tcount?: number;
  };
};

const agent = new https.Agent({
  rejectUnauthorized: false,
});

// const API_CLICKSHARE_DESKTOPBETAAPP_BASE_URL = apiurl.getApiRootPath();
// const API_CLICKSHARE_DESKTOPBETAAPP_BASE_URL = "http://dlc3.barco.com:3001/apis/clickshare/desktopbetaapp/";

const axiosInstance = axios.create({
  httpsAgent: new https.Agent({
    rejectUnauthorized: false,
  }),
  baseURL: apiurl.getApiRootPath(),
  responseType: "json",
  headers: {
    "Content-Type": "application/json; charset=UTF-8",
  },
});

const getComments = async (
  dateRange: { from: Date; to: Date },
  recordRange: { start: number; count: number }
): Promise<{ list: Comment[]; count: number }> => {
  const params = new URLSearchParams();
  params.append(
    "filters",
    JSON.stringify({
      view: "list",
      daterange: {
        start: dayjs(dateRange.from).startOf("day").toISOString(),
        end: dayjs(dateRange.to).endOf("day").toISOString(),
      },
      recordrange: recordRange,
    })
  );
  const result: AxiosResponse<BiResponse<Comment[]>> = await axiosInstance.post(
    apiurl.getAPIurl("cxdesktopapp_comment_history"),
    params,
    {
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
    }
  );

  const { code, msg, datas = [], tcount = 0 } = result.data.result;
  if (code < 0) {
    throw new Error(msg);
  }
  return { list: datas, count: tcount };
};

const downloadComments = (dateRange: { from: Date; to: Date }): void => {
  const params = {
    filters: {
      view: "list",
      daterange: {
        start: dayjs(dateRange.from).startOf("day").toISOString(),
        end: dayjs(dateRange.to).endOf("day").toISOString(),
      },
    },
  };
  downloadWithForm(apiurl.getApiRootPath().concat(apiurl.getAPIurl("cxdesktopapp_comment_historydownload")), params);
};

export { getComments, downloadComments };
