import { useState, useCallback, useMemo } from "react";
import useFetch from "./useFetch";

export type RecordRange = { start: number; count: number };
// eslint-disable-next-line @typescript-eslint/no-explicit-any
type ApiFunction = (recordRange: RecordRange) => Promise<any>;
type UnPromisify<T> = T extends Promise<infer U> ? U : never;
type ApiReturnType<T extends ApiFunction> = UnPromisify<ReturnType<T>>;
type Props<T extends ApiFunction> = {
  api: T;
  countResultKey: keyof ApiReturnType<T>;
  dataResultKey: keyof ApiReturnType<T>;
  recordsPerPage: number;
};

type ReturnProps<T> = {
  data: T[];
  page: number;
  total: number;
  totalPages: number;
  gotoPage: (page: number) => void;
  error: string;
  isLoading: boolean;
};

function useIterator<T extends ApiFunction, S>(props: Props<T>): ReturnProps<S> {
  const { api, countResultKey, dataResultKey, recordsPerPage } = props;
  const [page, setPage] = useState<number>(0);
  const { data: response, fetch, error, isLoading } = useFetch<ApiFunction>(api);

  const gotoPage = useCallback(
    (pageToFetch) => {
      if (pageToFetch > 0) {
        setPage(pageToFetch);
        fetch({ start: (pageToFetch - 1) * recordsPerPage, count: recordsPerPage });
      }
    },
    [fetch, recordsPerPage]
  );

  const data = useMemo(() => response?.[dataResultKey] || [], [dataResultKey, response]);
  const total = useMemo(() => response?.[countResultKey] || 0, [countResultKey, response]);

  return {
    data,
    page,
    total,
    totalPages: Math.ceil(total / recordsPerPage),
    gotoPage,
    error,
    isLoading,
  };
}

export default useIterator;
