import React from "react";
import cx from "classnames";
import ReactDOM from "react-dom";
import classes from "./index.module.scss";

type Action = {
  text: string;
  className?: string;
  action: () => void;
};

type Props = {
  title: string;
  element?: HTMLElement;
  show: boolean;
  className?: string;
  actions: Action[];
  children: React.ReactNode;
  onClose: () => void;
};

function ModalDialog(props: Props): JSX.Element | null {
  const { title, show, className, element, onClose, actions } = props;

  const dialog = (
    <div className={cx(className, classes.container, "modal", { "is-active": show })}>
      <div className="modal-background" />
      <div className="modal-card">
        <header className="modal-card-head">
          <p className="modal-card-title" style={{ fontWeight: "bold" }}>
            {title}
          </p>
          <button type="button" className={cx("delete", classes.closebtn)} aria-label="close" onClick={onClose} />
        </header>
        <section className="modal-card-body" style={{ height: 300 }}>
          {props.children}
        </section>
        <footer className={cx("modal-card-foot", classes.fletfromend)}>
          {actions.map(({ text: btnText, className: btnClassName, action }, index) => {
            return (
              <button
                type="button"
                key={index.toString()}
                className={cx("button", btnClassName, classes.dialogbtn)}
                onClick={action}
              >
                {btnText}
              </button>
            );
          })}
        </footer>
      </div>
    </div>
  );

  if (element) {
    return ReactDOM.createPortal(dialog, element);
  }
  return dialog;
}

export default ModalDialog;
