import React, { useMemo } from "react";
import cx from "classnames";
import dayjs from "dayjs";
import emojimaps from "../../common/emotions/emotions";
import classes from "./CommentCard.module.scss";
import { ReactComponent as Happy1Icon } from "../../assets/sentimental/happy(1).svg";
import { ReactComponent as Happy2Icon } from "../../assets/sentimental/happy(2).svg";
import { ReactComponent as Happy3Icon } from "../../assets/sentimental/happy(3).svg";
import { ReactComponent as Happy4Icon } from "../../assets/sentimental/happy(4).svg";
import { ReactComponent as Happy5Icon } from "../../assets/sentimental/happy(5).svg";
import { ReactComponent as NeutralIcon } from "../../assets/sentimental/neutral.svg";
import { ReactComponent as Sad1Icon } from "../../assets/sentimental/sad(1).svg";
import { ReactComponent as Sad2Icon } from "../../assets/sentimental/sad(2).svg";
import { ReactComponent as Sad3Icon } from "../../assets/sentimental/sad(3).svg";
import { ReactComponent as Sad4Icon } from "../../assets/sentimental/sad(4).svg";
import { ReactComponent as Sad5Icon } from "../../assets/sentimental/sad(5).svg";
import { ReactComponent as UnknownIcon } from "../../assets/sentimental/unknown.svg";
import { Comment } from "../../api/bi-service";

const renderFace = (score: number) => {
  switch (true) {
    case score == null:
      return { text: emojimaps[11], icon: <UnknownIcon /> };
    case score >= 5:
      return { text: emojimaps[10], icon: <Happy5Icon /> };
    case score >= 4:
      return { text: emojimaps[9], icon: <Happy4Icon /> };
    case score >= 3:
      return { text: emojimaps[8], icon: <Happy3Icon /> };
    case score >= 2:
      return { text: emojimaps[7], icon: <Happy2Icon /> };
    case score > 0:
      return { text: emojimaps[6], icon: <Happy1Icon /> };
    case score === 0:
      return { text: emojimaps[5], icon: <NeutralIcon /> };
    case score >= -1:
      return { text: emojimaps[4], icon: <Sad1Icon /> };
    case score >= -2:
      return { text: emojimaps[3], icon: <Sad2Icon /> };
    case score >= -3:
      return { text: emojimaps[2], icon: <Sad3Icon /> };
    case score >= -4:
      return { text: emojimaps[1], icon: <Sad4Icon /> };
    case score >= -5:
      return { text: emojimaps[0], icon: <Sad5Icon /> };
    default:
      return { text: emojimaps[5], icon: <NeutralIcon /> };
  }
};

function CommentCard(props: Comment): JSX.Element {
  const { emscore, commentid, osinfo, appversion, eventtime, comment, rating } = props;
  const face = useMemo(() => renderFace(emscore), [emscore]);
  return (
    <div className={cx("card", classes.container)}>
      <div className={classes.titlebar}>
        <div className={classes.content}>
          <div className={classes.field}>
            <div className={classes.title}>Event ID</div>
            <div className={classes.value} style={{ fontWeight: "bold" }}>
              {commentid}
            </div>
          </div>
          <div className={classes.field}>
            <div className={classes.title}>OS</div>
            <div className={classes.value}>{osinfo}</div>
          </div>
          <div className={classes.field}>
            <div className={classes.title}>App Version</div>
            <div className={classes.value}>{appversion}</div>
          </div>
          <div className={classes.field}>
            <div className={classes.title}>Rating</div>
            <div className={classes.value}>{rating}</div>
          </div>
          <div className={classes.field}>
            <div className={classes.title} style={{ textAlign: "right" }}>
              Event Time
            </div>
            <div className={classes.value} style={{ textAlign: "right" }}>
              {dayjs(eventtime).format("YYYY-MM-DD HH:mm:ss")}
            </div>
          </div>
        </div>
      </div>
      <div className={classes.contentbar}>
        <div className={classes.content}>
          <span className={classes.sentiment} style={{ display: "none" }}>
            <div className={classes.icon}>{face.icon}</div>
            <div className={classes.text}>
              {face.text}
              <br />
              {emscore}
            </div>
          </span>
          <div className={classes.field}>
            <div className={classes.title}>Comment</div>
            <div className={classes.value}>{comment}</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CommentCard;
