import React from "react";
import cx from "classnames";
import classes from "./LeftBar.module.scss";
import siteLogo from "../../../../assets/common/sitelogo.png";
import { ReactComponent as MenuSentiIcon } from "../../../../assets/common/icons/sentimentalMenu.svg";
import { ReactComponent as MenuDashboardIcon } from "../../../../assets/common/icons/dashboard.svg";
import { ReactComponent as MenuHistoryIcon } from "../../../../assets/common/icons/list.svg";

const handleLinkMenuClick = (e: any) => {
  e.preventDefault();
  if (e.currentTarget.id === "btntodashboard") globalThis.window.open("/clickshare/comment/dashboard", "_blank");
  if (e.currentTarget.id === "btntohistory") globalThis.window.open("/clickshare/comment/history", "_blank");
};

function LeftBar(): JSX.Element {
  return (
    <div className={classes.bar}>
      <div className={classes.titlebar}>
        <div className={classes.sitelogo}>
          <img src={siteLogo} alt="" />
        </div>
        <div className={classes.title}>PEPS</div>
      </div>
      <div className={classes.sidemenu}>
        <div className={classes.menu}>ClickShare Desktop App</div>
        <div className={classes.submenu}>
          <MenuSentiIcon />
          <div className={classes.title}>Comment</div>
        </div>
        <button
          type="button"
          id="btntodashboard"
          className={classes.linkmenu}
          onClick={handleLinkMenuClick}
          style={{ display: "none" }}
        >
          <div>
            <MenuDashboardIcon />
            <div className={classes.title}>Dashboard</div>
          </div>
        </button>
        <button type="button" id="btntohistory" className={classes.linkmenu} onClick={handleLinkMenuClick}>
          <div>
            <MenuHistoryIcon />
            <div className={classes.title}>Comment Logs</div>
          </div>
        </button>
        <div className={classes.menuhoffset} />
      </div>
    </div>
  );
}
export default LeftBar;
