/* eslint-disable no-nested-ternary */
import React, { useCallback, useMemo, useState } from "react";
import cx from "classnames";
import dayjs from "dayjs";
import classes from "./history.module.scss";
import DateRangePicker, { DateRange } from "../../../common/components/DateRangePicker";
import { getComments, downloadComments, Comment } from "../../../api/bi-service";
import LeftBar from "./leftbar/LeftBar";
import CommentCard from "../../../components/cards/CommentCard";
import PageNav from "../../../common/components/PageNav";
import useIterator, { RecordRange } from "../../../common/hooks/useFetchIterator";
import ModalDialog from "../../../common/components/ModalDialog";
import { ReactComponent as MenuHistoryIcon } from "../../../assets/common/icons/list.svg";
import { ReactComponent as SearchIcon } from "../../../assets/search.svg";
import { ReactComponent as ExportIcon } from "../../../assets/download.svg";

const RECORDS_PER_PAGE = 10;

function Comments(): JSX.Element {
  const [dateRange, setDateRange] = useState<DateRange>({
    from: dayjs(new Date()).subtract(1, "week").toDate(),
    to: new Date(),
  });
  const [exportDateRange, setExportDateRange] = useState<DateRange>({ from: new Date(), to: new Date() });
  const [showExportForm, setShowExportForm] = useState<boolean>(false);
  const dialogElement = useMemo(() => {
    const element = document.createElement("div", {});
    document.body.append(element);
    return element;
  }, []);

  const api = useCallback(
    (recordRange: RecordRange) => {
      return getComments(dateRange, recordRange);
    },
    [dateRange]
  );
  const { data, page, totalPages, gotoPage, error, isLoading } = useIterator<typeof api, Comment>({
    api,
    countResultKey: "count",
    dataResultKey: "list",
    recordsPerPage: RECORDS_PER_PAGE,
  });

  const handleShowExportForm = useCallback(() => {
    setExportDateRange({ ...dateRange });
    setShowExportForm(true);
  }, [dateRange]);

  const handleExport = useCallback(() => {
    downloadComments(exportDateRange);
    setShowExportForm(false);
  }, [exportDateRange]);

  return (
    <div className={classes.container}>
      <div className={classes.leftpanel}>
        <LeftBar />
      </div>
      <div className={classes.chartpanel}>
        <div className={classes.rowarea_title}>
          <div className={classes.prodtitle}>
            <MenuHistoryIcon />
            <div className={classes.title}>ClickShare Desktop App</div>
          </div>
          <div className={classes.charttitle}>User Rating and Comments review</div>
        </div>
        <div className={classes.rowarea_content}>
          <div className={classes.chartarea}>
            <div className={classes.content} style={{ height: "auto" }}>
              <div className={classes.toolbar}>
                <DateRangePicker value={dateRange} onChange={setDateRange} />
                <button
                  type="button"
                  disabled={!dateRange.from || !dateRange.to}
                  className="button"
                  onClick={() => {
                    gotoPage(1);
                  }}
                >
                  <span className="icon is-small">
                    <SearchIcon title="Search" />
                  </span>
                </button>
                <button type="button" className={cx("button", classes.export)} onClick={handleShowExportForm}>
                  <span className="icon is-small">
                    <ExportIcon title="Download CSV" />
                  </span>
                </button>
              </div>
              <div className={cx(classes.comments, "box")}>
                {isLoading ? (
                  <span className={cx(classes.info, classes.loadinginfo)}>Loading...</span>
                ) : error ? (
                  <span className={cx(classes.info, classes.errorinfo)}>{error}</span>
                ) : data.length <= 0 ? (
                  <span className={cx(classes.info, classes.noavailinfo)}>No Comments Available</span>
                ) : (
                  data.map((item) => {
                    // eslint-disable-next-line react/jsx-props-no-spreading
                    return <CommentCard key={item.commentid} {...item} />;
                  })
                )}
              </div>
              <PageNav className={classes.pagination} pages={totalPages} current={page} onChange={gotoPage} />
              <ModalDialog
                element={dialogElement}
                title="Download CSV"
                show={showExportForm}
                onClose={() => {
                  setShowExportForm(false);
                }}
                actions={[{ text: "Export", action: handleExport }]}
              >
                <DateRangePicker value={exportDateRange} onChange={setExportDateRange} />
              </ModalDialog>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Comments;
